<template>
    <main>
        <div class="container">
            <div class="flex left">
                <div class="top">
                    <img src="https://cdn.migu.cn/12001/upload/column/20190903145054502135.png" alt="" class="small">
                    <span>热点</span>
                </div>
                <div>
                    <span>最新</span>
                    <span>猜你喜欢</span>
                </div>
            </div>
            <div class="flex left">
                <div class="rd" v-for="(t,i) of list" :key=i  @click="detail" :data-lid="t.lid">
                    <img :src="t.imgsm" alt="" :data-lid="t.lid">
                    <p>{{t.total}}</p>
                </div>
            </div>
        </div>
    </main>
</template>
<style scoped>
    .container{
        width:1200px;
        margin: 0 auto}
    .flex{
        display:flex;
        justify-content: space-between !important;
        flex-wrap: wrap
        }
    .left{
        justify-content: flex-start;
        }
    .rd>img{
        width:100%;height:140px;
        }
    .rd>img:hover{
        transform: scale(1.1);
        transition-duration: 1s
        }
    p{
        margin:5px 0;
        width:100%;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space:nowrap;
        }
    .rd{
        overflow:hidden;
        width:250px;
        height:200px;
        }
    .top{
        margin-top:20px
        }
    .top>img{
        vertical-align: bottom
        }
    .top>span{
        display:inline-block;
        margin-left:10px;
        font-size:18px;
        font-weight:bolder
        }
</style>
<script>
    export default{
        data(){
            return{
                list:[]
            }
        },
        methods:{
            detail(e){
                var lid=e.target.dataset.lid
                this.$router.push('/rd_detail?lid='+lid)
            }
        },
        created(){
            //获取热点页的内容
            this.axios.get("rd")
            .then(
                res=>{
                    this.list=res.data.data
                    console.log(res)
                }
            )
            .catch(err=>{console.log(err)})
        },
    }
</script>