<template>
    <main>
        <video :src="list[0].path" controls="controls" class="container" v-if='list.length>0'></video>
    </main>
</template>
<style scoped>
    .container{width:80%;height:600px}
</style>
<script>
    export default{
        data(){
            return{
                list:[]
            }
        },
        methods:{

        },
        created(){
            var id=this.$route.query.id
            var obj={
                id:id
            }
            this.axios.get("/nvedio",{params:obj})
            .then(res=>{
                this.list=res.data.data
                console.log(this.list)
                }
            )
            .catch(err=>{
                console.log(err)
            })
        },
    }
</script>