                <li @mouseenter="enter" @mouseleave="leave">
                    <h3 class="inline">我们的歌：</h3>
                    <span>肖战化身Dj现场打碟</span>
                </li>
                <li @mouseenter="enter" @mouseleave="leave">
                    <h3 class="inline">我们的歌：</h3>
                    <span>肖战化身Dj现场打碟</span>
                </li>
                <li @mouseenter="enter" @mouseleave="leave">
                    <h3 class="inline">我们的歌：</h3>
