    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  img{
      width:100%;
      height:100%
      }
  .bar{
      position:absolute;
      top:70px;
      right:100px;
        margin:10px 0;z-index: 9999;
        border-radius:5px;text-align:start;opacity:0.5;
        padding:5px 0;background:#475669;
        opacity:0.5;
        color:white;border-radius:5px
        }
  .block{
      display:block
      }
  .inline{
