</style>
<script>
    export default{
        data(){
            return{
                lunbo:[]
            }
        },
        methods:{
            leave(e){
               e.target.classList.remove("change")
            },
            enter(e){
                e.target.classList.add("change")
            }
        },
        created(){
            this.axios.get("lunbo")
            .then(res=>{
                this.lunbo=res.data.data
                console.log(res)
            })
            .catch(err=>{
                console.log(err)
            })
        },
